<template>
	<div class="container-fluid">
		<div class="row">
			<nav class="col-md-2 d-none d-md-block bg-light sidebar">
	          	<div class="sidebar-sticky">
	            	<ul class="nav flex-column">
	              		<li class="nav-item" v-for="item in list">
	                		<router-link :to="item.router" class="nav-link active">
	                  			{{item.text}}
	                		</router-link>
	              		</li>
	            	</ul>
	          	</div>
        	</nav>
        	<div class="col-md-9 ml-sm-auto col-lg-10 pt-3 px-4 col-md-offset-2">
            <router-view @index="index" :msg="data"></router-view>
        	</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'Container',
		data(){
			return{
				list:[
					{text:'活动管理',router:'/combination/activity'},
					{text:'战队',router:'/combination/match'},
          {text:'商户码',router:'/combination/merchantcode'},
					// {text:'会员管理',router:'/combination/member'},
				],
        data:''
			}
		},
    methods:{
      index:function(data){
        // console.log(data)
        this.data=data;
        console.log(this.data)
      }
    }
	}
</script>
<style scoped>
	.col-md-9{
		padding-top: 70px;
	}
</style>



