<template>
  <div class="matchedit">
    <main role="main" class="container-fluid">
      <div class="my-3 p-3 bg-white rounded box-shadow">
        <h6 class="border-bottom border-gray pb-2 mb-0">赛程相关</h6>
        <div class="text-muted pt-3">
          <div v-for="(item,index) in array">
            <div class="row">
              <div class="col-md-12 dimen">
                <span>名称：</span><p>{{item.name}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 dimen">
                <span>举办城市：</span><p>{{item.city}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 dimen">
                <span>地址：</span><p>{{item.address}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 dimen">
                <span>开始时间：</span><p>{{item.begin_at}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 dimen">
                <span>结束时间：</span><p>{{item.end_at}}</p>
              </div>
            </div>

            <div class="row">
              <div class="col-md-12 dimen">
                <!--<span>状态：</span> <p></p>-->
                <button class="btn btn-danger" data-toggle="modal" data-target="#myModify" @click="modify(index)">修改赛程</button>
              </div>
            </div>
            <!--修改赛程弹窗-->
            <div class="modal fade" tabindex="-1" id="myModify" role="dialog">
              <div class="modal-dialog" role="document">
                <div class="modal-content">
                  <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" >修改赛程</h4>
                  </div>
                  <div class="modal-body" >
                    <div class="row">
                      <div class="col-md-6 dimen2">
                        <span>赛程名称：</span><input v-bind:value="array2.name" type="text" id="name1" class="form-control"  placeholder="">
                      </div>
                      <div class="col-md-6 dimen2">
                        <span>开始时间：</span><input v-bind:value="array2.begin_at" type="text" id="city1" class="form-control"  placeholder="">
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-6 dimen2"><span>举办城市：</span><input v-bind:value="array2.city" type="text" id="prize1" class="form-control"  placeholder=""></div>
                      <div class="col-md-6 dimen2"><span>结束时间：</span><input v-bind:value="array2.end_at" type="text" id="contact1" class="form-control"  placeholder=""></div>
                    </div>
                    <div class="row">
                      <div class="col-md-6 dimen2"><span>地址：</span><input v-bind:value="array2.address" type="text" id="organizer1" class="form-control"  placeholder=""></div>
                      <div class="col-md-6 dimen2"><span>ID：</span><input v-bind:value="item.id" type="text" id="id" class="form-control"  placeholder="" readonly></div>
                    </div>

                  </div>
                  <div class="modal-footer">

                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" data-dismiss="modal" @click="update">提交</button>
                  </div>
                </div><!-- /.modal-content -->
              </div><!-- /.modal-dialog -->
            </div><!-- /.modal -->

          </div>
        </div>

        <button class="btn btn-primary" data-toggle="modal" data-target="#myModal">创建赛程</button>

        <!--创建赛程弹窗-->
        <div class="modal fade" tabindex="-1" id="myModal" role="dialog" >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                <h4 class="modal-title" id="gridSystemModalLabel">发布活动</h4>
              </div>
              <div class="modal-body">
                <div class="row">
                  <div class="col-md-6 dimen2">
                    <span>赛程名称：</span><input  type="text" id="name" class="form-control"  placeholder="">
                  </div>
                  <div class="col-md-6 dimen2">
                    <span>开始时间：</span><input  type="date" id="city" class="form-control"  placeholder="">
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-6 dimen2"><span>举办城市：</span><input  type="text" id="prize" class="form-control"  placeholder=""></div>
                  <div class="col-md-6 dimen2"><span>结束时间：</span><input  type="date" id="contact" class="form-control"  placeholder=""></div>
                </div>
                <div class="row">
                  <div class="col-md-6 dimen2"><span>地址：</span><input  type="text" id="organizer" class="form-control"  placeholder=""></div>
                  <div class="col-md-6 dimen2"><span>ID：</span><input v-bind:value="idea" type="text" id="cellphone" class="form-control"  placeholder="" readonly></div>
                </div>

              </div>
              <div class="modal-footer">

                <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                <button type="button" class="btn btn-primary" data-dismiss="modal" @click="upload">提交</button>
              </div>
            </div><!-- /.modal-content -->
          </div><!-- /.modal-dialog -->
        </div><!-- /.modal -->

      </div>
    </main>
  </div>
</template>

<script>
  export default {
    name: "match-edit",
    props:['msg'],
    data(){
      return{
        array:[],
        idea:'',
        array2:[],
      }
    },
    watch:{
      msg:function(e){
        console.log(e)
      }
    },
    created(e){
      this.activityItem();
    },
    methods:{
      activityItem:function(){
        var that = this;
        console.log(that.$route.query.id);
        that.$axios.get('https://dc.3128play.com/api/activity/'+that.$route.query.id).then((res)=>{
          console.log(res);
          that.array=res.data.data.schedules;
          that.idea = res.data.data.id;
        })
      },
      update:function () {
        var that=this;
        that.$axios.post('https://dc.3128play.com/api/admin/schedule/edit',that.$qs.stringify({
          schedule_id:$('#id').val(),
          name:$('#name1').val(),
          begin_at:$('#city1').val(),
          end_at:$('#contact1').val(),
          city:$('#prize1').val(),
          address:$('#organizer1').val()
        })).then((res)=>{
          console.log(res)
          that.activityItem();
        })

      },

      modify:function (index) {
        var that=this
          // console.log(that.array[index]);
          that.array2=that.array[index]
        },
        upload:function () {
          var that = this;
          that.$axios.post('https://dc.3128play.com/api/admin/schedule/create',that.$qs.stringify({
            activity_id:$('#cellphone').val(),
            name:$('#name').val(),
            begin_at:$('#city').val(),
            end_at:$('#contact').val(),
            city:$('#prize').val(),
            address:$('#organizer').val()
          })).then((res)=>{
            console.log(res);
            that.activityItem();
          }).catch(error=>{
            console.log(error);
          })
        },
        Submit:function () {
          this.$axios.post('https://dc.3128play.com/api/schedule/edit',that.$qs.stringify({

          })).then((res)=>{
            console.log(res);
          }).catch(error=>{
            console.log(error)
          })
        }
      }
    }
  </script>

  <style scoped>
    .bia{
      margin-right: 3px;
    }
    .zix{
      margin-top: 20px;
    }
    .dimen{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }
    .dimen span{
      width: 75px;
      text-align: right;
    }
    .dimen p,textarea{
      font-size: 12px;
      margin: 0;
    }
    .dimen2{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }
    .dimen2 span{
      width: 112px;
      text-align: right;
    }
    .dimen2 p,textarea{
      font-size: 12px;
      margin: 0;
    }
    .position-file{
      margin-top: 10px;
    }
    textarea{
      margin-bottom: 10px;
    }
    .hide{display: none}
    .position-file>input{
      position: relative;
      top: -29px;
      opacity: 0;
    }
    .reds{
      color: red;
      font-size: 20px;
      line-height: 0;
    }
    .foui{
      margin-top: 15px;
    }
    .newly{
      margin-top: 10%;
    }
    h6{
      font-size: 17px;
      font-weight: 700;
      color: #6c757d!important;
    }
    .pb-2{
      padding-bottom: .5rem!important;
    }
    .p-3 {
      padding: 1rem!important;
    }
    .text-muted {
      color: #6c757d!important;
    }
    .pb-3, .py-3 {
      padding-bottom: 1rem!important;
    }
    .d-block {
      display: block!important;
    }
    .media {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
    }
    .mr-2, .mx-2 {
      margin-right: .5rem!important;
    }
    .rounded {
      border-radius: .25rem!important;
    }
    .bg-white {
      background-color: #fff!important;
    }

    html,
    body {
      overflow-x: hidden; /* Prevent scroll on narrow devices */
    }

    body {
      padding-top: 56px;
    }

    @media (max-width: 767.98px) {
      .offcanvas-collapse {
        position: fixed;
        top: 56px; /* Height of navbar */
        bottom: 0;
        width: 100%;
        padding-right: 1rem;
        padding-left: 1rem;
        overflow-y: auto;
        /*background-color: var(--gray-dark);*/
        transition: -webkit-transform .3s ease-in-out;
        transition: transform .3s ease-in-out;
        transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
        -webkit-transform: translateX(100%);
        transform: translateX(100%);
      }
      .offcanvas-collapse.open {
        -webkit-transform: translateX(-1rem);
        transform: translateX(-1rem); /* Account for horizontal padding on navbar */
      }
