<template>
    <div class="process">
      <main role="main" class="container-fluid">

        <div class="my-3 p-3 bg-white rounded box-shadow " >
          <h6 class="border-bottom border-gray pb-2 mb-0">修改</h6>
          <div class=" text-muted pt-3" >
            <form >
              <div class="form-group col-sm-6 ">
                <div class="balance">
                  <label >活动名称：</label>
                  <input  type="text" v-bind:value="array.name" class="form-control " id="title" placeholder="">
                </div>
                <div class="balance">
                  <label >活动奖品：</label>
                  <input type="text" v-bind:value="array.prize" id="jp" class="form-control"/>
                  <!--<textarea  class="form-control" rows="1" id="jp" placeholder="内容">{{array.prize}}</textarea>-->
                </div>
                <div class="balance">
                  <label >主办方：</label>
                  <input  type="text" v-bind:value="array.organizer" id="zb" class="form-control"  placeholder="">
                </div>
                <div class="balance">
                  <label >举办城市：</label>
                  <input  type="text" v-bind:value="array.city" id="cs" class="form-control "  placeholder="">
                </div>
                <div class="balance">
                  <label >赞助商：</label>
                  <input type="text" v-bind:value="array.sponsor" id="zzs" class="form-control"/>
                  <!--<textarea  class="form-control" rows="1" id="zzs" placeholder="">{{array.sponsor}}</textarea>-->
                </div>
              </div>
              <div class="col-sm-6">
                <div class="balance">
                  <label >联系人：</label>
                  <input  type="text" v-bind:value="array.contact" id="lx" class="form-control "  placeholder="">
                </div>
                <div class="balance">
                  <label >电话：</label>
                  <input  type="text" v-bind:value="array.cellphone" id="dh" class="form-control "  placeholder="">
                </div>
                <div class="balance">
                  <label >开始时间：</label>
                  <input  type="date" v-bind:value="array.begin_at" id="ks" class="form-control "  placeholder="">
                </div>
                <div class="balance">
                  <label >截止时间：</label>
                  <input  type="date" v-bind:value="array.end_at" id="jz" class="form-control "  placeholder="">
                </div>
                <input style="opacity: 0" type="text" v-bind:value="array.id" id="id" class="form-control " readonly placeholder="">
              </div>
              <div class="col-md-12">
                <div class="balance">
                  <label style="width: 86px">活动规则：</label>
                  <textarea  class="form-control" rows="4" id="gz" placeholder="内容">{{array.detail}}</textarea>
                </div>
              </div>
              <div class="row">
                <div class="col-sm-4">
                  <label >封面图片：</label>
                  <el-upload
                    class="avatar-uploader"
                    action="https://dc.3128play.com/api/upload-image"
                    name="img"
                    :headers="uploadHeader"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl"  class="avatar imgfm">
                    <img v-else v-bind:src="array.cover"  class="avatar imgfm"/>
                  </el-upload>
                </div>
                <div class="col-md-6">
                  <div>
                    <label>详情图片：</label>
                  </div>
                  <el-upload
                    class="avatar-uploader"
                    action="https://dc.3128play.com/api/upload-image"
                    name="img"
                    :headers="uploadHeader"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess2"
                    :before-upload="beforeAvatarUpload2">
                    <img v-if="imageUrl2" :src="imageUrl2" class="avatar imgxq">
                    <img v-else v-bind:src="array.detail_images" id="imgqx" class="avatar imgxq"/>
                  </el-upload>

                </div>
              </div>
              <button type="submit" class="btn btn-primary foui" @click="modify">提交</button>
            </form>

          </div>


        </div>

        <!--<img v-bind:src="msg.cover" alt="" class="mr-2 rounded">-->
      </main>
    </div>
</template>

<script>
    export default {
        name: "process",
        props:['msg'],
      data(){
          return{
            array:[],
            array2:[],
            imageUrl:'',
            imageUrl2:'',
            uploadHeader: {
              Accept: 'application/json',
              Authorization: 'Bearer '+localStorage.token
            },
            imgs:'',
            imgs2:'',
          }
      },

      watch:{
          msg:function(e){
            console.log(e)
          }
      },
      //活动详情
      created(){
        var that = this;
        console.log(that.$route.query.id)
        that.$axios.get('https://dc.3128play.com/api/activity/'+that.$route.query.id,{},{}).then((res)=>{
          console.log(res.data.data);
          that.array = res.data.data;
          that.array2 = res.data.data.corps;
        })
      },
      methods:{

          //修改活动
        modify:function () {
          var that=this;
          console.log($('.imgfm')[0].src)
          if ($('#id').val()==""||$('#title').val()==""){
            console.log("必填项为空，修改失败！")
          }else if($('#ks').val()==""||$('#jz').val()==""){
            console.log("必填项为空，修改失败！")
          }else if ($('#gz').val()==""||$('#jp').val()==""){
            console.log("必填项为空，修改失败！")
          }else {
            that.$axios.post('https://dc.3128play.com/api/admin/activity/edit',that.$qs.stringify({
              activity_id:$('#id').val(),
              name:$('#title').val(),
              cover:$('.imgfm')[0].src,
              begin_at:$('#ks').val(),
              end_at:$('#jz').val(),
              detail:$('#gz').val(),
              detail_images:$('.imgxq')[0].src,
              prize:$('#jp').val(),
              organizer:$('#zb').val(),
              sponsor:$('#zzs').val(),
              city:$('#cs').val(),
              contact:$('#lx').val(),
              cellphone:$('#dh').val(),
            })).then((res)=>{
              console.log(res);
              this.$router.push({path:'../combination/activity'});
            }).catch(error=>{
              console.log(error)
            })
          }

        },

        //详情图片
        handleAvatarSuccess2(res, file) {
          this.imageUrl2 = file.response.data;
          this.imgs2=file.response.data;
          console.log(file.response.data)
        },
        beforeAvatarUpload2(file) {
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
          }
          if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
          }
          return isJPG && isLt2M;
        },
        //封面图片
        handleAvatarSuccess(res, file) {
          this.imageUrl = file.response.data;
          this.imgs=file.response.data;
          console.log(file.response.data)
        },
        beforeAvatarUpload(file) {
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
          }
          if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
          }
          return isJPG && isLt2M;
        }
      }
    }
</script>

<style scoped>
  label{width: 100px}
  .balance{
    margin-top: 5px;
    display: flex;
    align-items: center;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .position-file{
    margin-top: 10px;
  }
  textarea{
    margin-bottom: 10px;
  }
  .hide{display: none}
  .position-file>input{
    position: relative;
    top: -29px;
    opacity: 0;
  }
  .reds{
    color: red;
    font-size: 20px;
    line-height: 0;
  }
  .foui{
    margin-top: 15px;
  }
  .newly{
    margin-top: 10%;
  }
  h6{
    font-size: 17px;
    font-weight: 700;
    color: #6c757d!important;
  }
  .pb-2{
    padding-bottom: .5rem!important;
  }
  .p-3 {
    padding: 1rem!important;
  }
  .text-muted {
    color: #6c757d!important;
  }
  .pb-3, .py-3 {
    padding-bottom: 1rem!important;
  }
  .d-block {
    display: block!important;
  }
  .media {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
  }
  .mr-2, .mx-2 {
    margin-right: .5rem!important;
  }
  .rounded {
    border-radius: .25rem!important;
  }
  .bg-white {
    background-color: #fff!important;
  }

  html,
  body {
    overflow-x: hidden; /* Prevent scroll on narrow devices */
  }

  body {
    padding-top: 56px;
  }

  @media (max-width: 767.98px) {
    .offcanvas-collapse {
      position: fixed;
      top: 56px; /* Height of navbar */
      bottom: 0;
      width: 100%;
      padding-right: 1rem;
      padding-left: 1rem;
      overflow-y: auto;
      /*background-color: var(--gray-dark);*/
      transition: -webkit-transform .3s ease-in-out;
      transition: transform .3s ease-in-out;
      transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
    .offcanvas-collapse.open {
      -webkit-transform: translateX(-1rem);
      transform: translateX(-1rem); /* Account for horizontal padding on navbar */
    }
  }

  .nav-scroller {
    position: relative;
    z-index: 2;
    height: 2.75rem;
    overflow-y: hidden;
  }

  .nav-scroller .nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    padding-bottom: 1rem;
    margin-top: -1px;
    overflow-x: auto;
    color: rgba(255, 255, 255, .75);
    text-align: center;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }

  .nav-underline .nav-link {
    padding-top: .75rem;
    padding-bottom: .75rem;
    font-size: .875rem;
    /*color: var(--secondary);*/
  }

  .nav-underline .nav-link:hover {
    /*color: var(--blue);*/
  }

  .nav-underline .active {
    font-weight: 500;
    /*color: var(--gray-dark);*/
  }

  .text-white-50 { color: rgba(255, 255, 255, .5); }

  /*.bg-purple { background-color: var(--purple); }*/

  .border-bottom { border-bottom: 1px solid #e5e5e5; }

  .box-shadow { box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); }

  .lh-100 { line-height: 1; }
  .lh-125 { line-height: 1.25; }
  .lh-150 { line-height: 1.5; }
</style>



