<template>
	<div class="merchant-code container-fluid">
		<transition name="slide-fade">
			<div v-show="alertFalg.falg" class="alert alert-success" role="alert"><h4 style="color: #468847">✔ {{alertFalg.msg}}</h4></div>
		</transition>
		<div class="form-inline page-header">
			<div class="form-group">
				<input v-model="countText" type="text" class="form-control" placeholder="请输入需要生成的数量">
			</div>
			<button @click="createdMaterial" type="button" class="btn btn-primary">生成商家码</button>
		</div>

		<div class="row cons">
			<div v-for="(item,index) in all_materialItem.data" class="tlu" style="cursor:pointer;">
				<div class="thumbnail">
					<canvas :id="'mCode'+index" height="200px" width="200px">
            您的浏览器不支持 HTML5 canvas 标签。
          </canvas>
					<div class="caption">
            <div style="display: flex;margin-bottom: 10px">
              <div>
                <span  v-if="item.mid==null" class="label label-primary">未绑定</span>
                <span v-else class="label label-danger">已绑定</span>
              </div>

              <p style="margin: 0 10px">{{item.code}}</p>
            </div>
						<p><a @click="mCode(index)" :download="item.code+'.png'" class="downBtn btn btn-primary" role="button">下载</a></p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'MerchantCode',
		data(){
			return{
				countText:'',
				all_materialItem:'',
				alertFalg:{
					falg:false,
					msg:''
				},
				mcode:''
			}
		},
		methods:{

			// 商家码加载
			loadMcode:function(){
				var that=this;
				// 先把全部图片都导入canvas里
				for(let i=0;i<that.all_materialItem.data.length;i++){
					// 获取每个canvas的id
					let mcode=$("#mCode"+i)[0];
					// 创建一个img标签
					let imgObj=new Image();
					// 打开跨域资源允许权限
					imgObj.crossOrigin="anonymous";
					// 设置img标签的src
					imgObj.src=that.all_materialItem.data[i].image;

					// 等img的图片加载完，再导入到canvas里
					imgObj.onload=function(){
						var ctx = mcode.getContext('2d');
						ctx.drawImage(imgObj, 0, 0,200,200);
					}
				}
			},
			// 商家码canvas转化
			mCode:function(index){
				var that=this;
				// 获取当前点击的canvas
				var mcode=$('#mCode'+index)[0];

				// 把canvas转化成base64链接，再加入到a标签的href里
				$('.downBtn').attr('href',mcode.toDataURL("image/png"));
			},
      //已生成的商家码
			all_material:function(){
				var that=this;
				this.$axios.post('https://dc.3128play.com/api/admin/material').then((res)=>{
					console.log(res);
					this.all_materialItem=res.data;
				}).then(()=>{
					that.loadMcode();
				})
			},
      //生成商家码
			createdMaterial:function(){
				var that=this;
				this.$axios.post('https://dc.3128play.com/api/admin/material/create',this.$qs.stringify({
					"count":this.countText||0
				})).then((res)=>{
					console.log(res)
					if(!res.data.code){
						that.alertFalg.falg=true;
						that.alertFalg.msg='商家码生成成功';
						that.all_material();
						setTimeout(function(){
							that.alertFalg.falg=false;
						},3000)
					}
				})
			}
		},
		mounted(){
			this.all_material();
		}
	}
</script>
<style scoped>
  .cons{
    display: flex;
    flex-wrap:wrap;
  }
  .tlu{
    width: 210px;
    margin: 0 10px;
  }
	.page-header{
		margin-top: 25px;
	}
	/* 可以设置不同的进入和离开动画 */
	/* 设置持续时间和动画函数 */
	.slide-fade-enter-active {
		transition: all .3s ease;
	}
	.slide-fade-leave-active {
		transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to
	/* .slide-fade-leave-active for below version 2.1.8 */ {
		transform: translateY(-20px);
		opacity: 0;
	}
	.col-md-9{
		padding-top: 70px;
	}
	.alert{
		position:fixed;
		top:60px;
		width:81%;
	}
	.alert>h4{
		margin:0;
		padding:10px;
	}
</style>



