<template>
	<div class="schedule">
		<div class="page-header double">
			<h1>赛程列表</h1>
      <button class="btn btn-info" data-toggle="modal" data-target="#myModal">创建赛程</button>
		</div>
		<div v-if="array.length!=0">
			<div v-for="item in array" class="jumbotron">
				<router-link :to="'/combination/scheduleitem?scheduid='+item.id+'&id='+activity_id">
					<h4>赛程名称：{{item.name}}</h4>
					<h5>举办城市：{{item.city}}</h5>
					<h5>起止时间：{{item.begin_at}}~{{item.end_at}}</h5>
				</router-link>
			</div>
		</div>
		<div v-else class="alert alert-danger" role="alert">没有赛程信息</div>


    <!--创建赛程弹窗-->
    <div class="modal fade" tabindex="-1" id="myModal" role="dialog" >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" id="gridSystemModalLabel">发布活动</h4>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6 dimen2">
                <span>赛程名称：</span><input  type="text" id="name" class="form-control"  placeholder="">
              </div>
              <div class="col-md-6 dimen2">
                <span>开始时间：</span><input  type="date" id="city" class="form-control"  placeholder="">
              </div>
            </div>
            <div class="row">
              <div class="col-md-6 dimen2"><span>举办城市：</span><input  type="text" id="prize" class="form-control"  placeholder=""></div>
              <div class="col-md-6 dimen2"><span>结束时间：</span><input  type="date" id="contact" class="form-control"  placeholder=""></div>
            </div>
            <div class="row">
              <div class="col-md-6 dimen2"><span>地址：</span><input  type="text" id="organizer" class="form-control"  placeholder=""></div>
              <div class="col-md-6 dimen2" style="opacity:0;"><span>ID：</span><input v-bind:value="idea" type="text" id="cellphone" class="form-control"  placeholder="" readonly></div>
            </div>

          </div>
          <div class="modal-footer">

            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="upload">提交</button>
          </div>
        </div><!-- /.modal-content -->
      </div><!-- /.modal-dialog -->
    </div><!-- /.modal -->

  </div>
</template>
<script>
	export default{
		name:'Schedule',
		data(){
			return{
				array:[],//赛程信息
				idea:'',//活动id
				activity_id:'',
			}
		},
		created(){
			this.activityItem();
		},
		methods:{
			// 活动详细信息

			activityItem:function(){
				var that = this;
				console.log(that.$route.query.id);
				this.activity_id=that.$route.query.id;
				that.$axios.get('https://dc.3128play.com/api/activity/'+that.$route.query.id).then((res)=>{
					console.log(res);
					that.array=res.data.data.schedules;
					that.idea = res.data.data.id;
				})
			},

      //提交创建赛程
      upload:function () {
        var that=this;
        console.log($('#cellphone').val())
        that.$axios.post('https://dc.3128play.com/api/admin/schedule/create',that.$qs.stringify({
          activity_id:$('#cellphone').val(),
          name:$('#name').val(),
          begin_at:$('#city').val(),
          end_at:$('#contact').val(),
          city:$('#prize').val(),
          address:$('#organizer').val()
        })).then((res)=>{
          console.log(res);
          this.activityItem();
        })
      }
		}
	}
</script>
<style scoped>
  .double{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
	.jumbotron{
		padding-top:18px;
		padding-bottom:18px;
		margin-bottom: 10px;
		padding-left: 35px;
		cursor: pointer;
		transition: all .3s;
	}
	a{
		color: #333;
		text-decoration: none;
	}
	.jumbotron:hover{
		box-shadow: 0 0 5px #a5a5a5;
	}
</style>



