<template>
  <div class="scheduleItem">
    <transition name="slide-fade">
      <div v-show="alertFalg.falg" class="alert alert-success" role="alert"><h4 style="color: #468847">✔ {{alertFalg.msg}}</h4></div>
    </transition>
    <transition name="slide-fade">
      <div v-show="alertFalg.falg1" class="alert alert-warning" role="alert"><h4 style="color:#8A6D3B">✔ {{alertFalg.msg}}</h4></div>
    </transition>

    <div class="jumbotron">
      <h3>赛程名称：{{addCorpList.name}}</h3>
      <div>举办城市：{{addCorpList.city}}</div>
      <div>举办地址：{{addCorpList.address}}</div>
      <div>起止时间：{{addCorpList.begin_at}}~{{addCorpList.end_at}}</div>
      <div><button @click="format" class="btn btn-info" data-toggle="modal" data-target="#myModify">修改赛程</button></div>
    </div>

    <div class="page-header">
      <h1><small>已添加的战队</small></h1>
    </div>
    <table class="table table-hover table-striped">
      <tr>
        <th>ID</th>
        <th>战队名称</th>
        <th>战队Logo</th>
        <th>名次</th>
        <th>积分</th>
        <th>操作</th>
      </tr>
      <tbody>
      <tr :class="[item.schedule_corp_status=='WIN'?'success alert-success':(item.schedule_corp_status=='ENTER'?'warning alert-warning':'danger alert-danger')]" v-for="(item,index) in addCorpList.corps">
        <th>{{item.id}}</th>
        <th>{{item.name}}</th>
        <th><img class="img-rounded logoImg" :src="item.logo"></th>
        <th>{{item.schedule_corp_rank}}</th>
        <th>{{item.point}}</th>

        <th>
          <p><button @click="removeCorpIn(index)" type="button" class="btn btn-danger">移除</button></p>
          <button @click="Setting(index)" type="button" class="btn btn-primary" data-toggle="modal" data-target="#SettingModal" data-whatever="@mdo">设置</button>
        </th>
        <!--<th><h4>{{item.schedule_corp_status}}</h4></th>-->
        <th v-if="item.schedule_corp_status=='WIN'"><h4>胜</h4></th>
        <th v-else-if="item.schedule_corp_status=='LOST'"><h4>负</h4></th>
        <th v-else><h4>比赛中</h4></th>
      </tr>
      </tbody>
    </table>


    <div class="page-header">
      <h1><small>已报名战队</small></h1>
    </div>
    <table class="table table-hover table-striped">
      <tr>
        <th>ID</th>
        <th>战队名称</th>
        <th>战队Logo</th>
        <th>成员数</th>
        <th>积分</th>
        <th>操作</th>
      </tr>
      <tr v-if="!item.ifFalg" v-for="(item,index) in list.corps">
        <th>{{item.id}}</th>
        <th>{{item.name}}</th>
        <th><img class="img-rounded logoImg" :src="item.logo"></th>
        <th>{{item.number}}</th>
        <th>{{item.point}}</th>
        <th>
          <button @click="addCorpIn(index)" type="button" class="btn btn-success">添加</button>
        </th>
      </tr>
    </table>

    <!-- 设置名次模态框 -->
    <div class="modal fade" tabindex="-1" role="dialog" id="SettingModal">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title">设置</h4>
          </div>
          <div class="modal-body row">
            <div class="media col-md-6">
              <div class="media-left">
                <a href="#">
                  <img style="width:64px" class="media-object" :src="settingItem.logo">
                </a>
              </div>
              <div class="media-body">
                <h4 class="media-heading">{{settingItem.name}}</h4>
                {{settingItem.detail}}
              </div>
            </div>
            <div class="col-md-6">
              <div class="input-group">
                <span class="input-group-addon" id="sizing-addon2">名次</span>
                <input id="ranking" type="text" :value="settingItem.schedule_corp_rank" class="form-control" placeholder="请输入名次" aria-describedby="sizing-addon2">
              </div>
              <div class="btn-group" style="margin-top:15px;display:flex;align-items:center">
                <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  选择胜负<span class="caret"></span>
                </button>
                <ul class="dropdown-menu">
                  <li v-for="(item,index) in dropdown" @click="getdown(index)"><a>{{item}}</a></li>
                </ul>
                <strong style="margin-left:10px">{{downText}}</strong>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
            <button @click="saveSetting" type="button" data-dismiss="modal" class="btn btn-primary">保存设置</button>
          </div>
        </div>
      </div>
    </div>

    <!--修改赛程弹窗-->
    <div class="modal fade" tabindex="-1" id="myModify" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" >修改赛程</h4>
          </div>
          <div class="modal-body" >
            <div class="row">
              <div class="col-md-6 dimen2">
                <span>赛程名称：</span><input v-bind:value="addCorpList.name" type="text" id="name1" class="form-control"  placeholder="">
              </div>
              <div class="col-md-6 dimen2">
                <span>开始时间：</span><input  type="datetime-local" :value="begin_at" id="city1" class="form-control"  placeholder="">
              </div>
            </div>
            <div class="row">
              <div class="col-md-6 dimen2"><span>举办城市：</span><input v-bind:value="addCorpList.city" type="text" id="prize1" class="form-control"  placeholder=""></div>
              <div class="col-md-6 dimen2"><span>结束时间：</span><input :value="end_at" type="datetime-local" id="contact1" class="form-control"  placeholder=""></div>
            </div>
            <div class="row">
              <div class="col-md-6 dimen2"><span>地址：</span><input v-bind:value="addCorpList.address" type="text" id="organizer1" class="form-control"  placeholder=""></div>
              <div class="col-md-6 dimen2" style="opacity:0;"><span>ID：</span><input v-bind:value="idea" type="text" id="id" class="form-control"  placeholder="" readonly></div>
            </div>

          </div>
          <div class="modal-footer">

            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="update">提交</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default{
    name:'ScheduleItem',
    data(){
      return{
        //已报名的战队
        list:[],
        // 已添加的战队
        addCorpList:[],
        idea:'',
        alertFalg:{
          falg:false,
          falg1:false,
          msg:''
        },
        settingItem:[],
        dropdown:['胜','负','比赛中'],
        dropdown1:['WIN','LOST','ENTER'],
        downText:'',
        rankText:'',
        //修改赛程时间格式
        begin_at:'',
        end_at:'',
        kais:'',
        jies:'',
        addCorpList2:[],
      }
    },
    created(){
       this.getSchedule();
    },

    methods:{
      //修改赛程时间设置格式
      format:function () {
        var begin_at=this.addCorpList.begin_at;
        var end_at=this.addCorpList.end_at;
        // console.log(begin_at.replace(/ /,'T'))
        this.begin_at=begin_at.replace(/ /,'T');
        this.end_at=end_at.replace(/ /,'T');
      },
      //提交修改赛程信息
      update:function () {

        var that=this;
        var kais=$('#city1').val();
        var jies=$('#contact1').val();
        this.kais=kais.replace(/T/,' ')+":00";
        this.jies=jies.replace(/T/,' ')+":00";
        console.log(kais);
        if ($('#id').val()==" "){
          console.log("修改失败！")
        }else if ($('#name1').val()==""){
          console.log("赛程名为空，修改失败！")
        }else if (that.kais==":00"){
          console.log("赛程时间为空1，修改失败！")
        }else if(that.jies==":00"){
          console.log("赛程时间为空2，修改失败！")
        }else if($('#prize1').val()==""){
          console.log("赛程城市为空，修改失败！")
        }else if($('#organizer1').val()==""){
          console.log("赛程地点为空，修改失败！")
        } else {
          that.$axios.post('https://dc.3128play.com/api/admin/schedule/edit',that.$qs.stringify({
            schedule_id:$('#id').val(),
            name:$('#name1').val(),
            begin_at:that.kais,
            end_at:that.jies,
            city:$('#prize1').val(),
            address:$('#organizer1').val()
          })).then((res)=>{
            console.log(res);
            this.getSchedule();
          })
        }
      },
      // 保存设置胜出战队及名次设置
      saveSetting:function(){
        var that=this;
        this.$axios.post('https://dc.3128play.com/api/admin/schedule/set',this.$qs.stringify({
          "schedule_corp_id":this.settingItem.schedule_corp_id,
          "status":this.dropdown1[this.downIndex],
          "rank":$('#ranking').val()
        })).then((res)=>{
          console.log(res);
          this.alertFalg.falg=true;
          this.alertFalg.msg='设置成功';
          this.getSchedule();
          setTimeout(function(){
            that.alertFalg.falg=false;
          },3000)
        })
      },
      // 获取下拉框的值
      getdown:function(index){
        console.log(index)
        this.downIndex=index;
        this.downText=this.dropdown[index];
      },
      // 设置
      Setting:function(index){
        this.settingItem=this.addCorpList.corps[index];
        this.downText='';
        console.log(this.settingItem);
        $('#exampleModal').on('show.bs.modal', function (event) {
          var button = $(event.relatedTarget);
          var recipient = button.data('whatever');
        })
      },
      // 获取报名的战队
      getSchedule:function(){
        var that=this;
        console.log(that.$route.query.scheduid)

        var addCorpList='';
        var addCorpList2='';
        var array=[];
        var index=[];
        var list='';

        // 获取添加到赛程的战队
        this.$axios.get('https://dc.3128play.com/api/admin/schedule/'+that.$route.query.scheduid).then(function(res){
          console.log(res);

          addCorpList=res.data.data;
          addCorpList2=addCorpList.corps;

          that.idea=res.data.data.id;

          for (var i=0;i<addCorpList2.length;i++){
            array.push(addCorpList2[i].id);
          }

          that.addCorpList=addCorpList;

        }).then(()=>{

          // 获取已报名的战队
          that.$axios.get('https://dc.3128play.com/api/admin/activity/'+that.$route.query.id).then(function(res){
            console.log(res);
            list=res.data.data;

            for(var i=0;i<list.corps.length;i++){
              for(var j=0;j<array.length;j++){
                if(list.corps[i].id==array[j]){
                  console.log(i)
                  index.push(i);
                  list.corps[i].ifFalg=true;
                }
              }
            }
            that.list=list;
          });

        })
      },

      // 点击添加战队到赛程
      addCorpIn:function(index){
        console.log(this.list.corps[index].id);
        var that=this;
        var xxx_id=this.list.corps[index].xxx_id;
        // 战队id
        var corpid=this.list.corps[index].id;
        // 赛程id
        var schedule_id=this.addCorpList.id;
        var corps=[{
          "xxx_id":xxx_id,
          "id":corpid
        }]
        this.$axios.post('https://dc.3128play.com/api/admin/schedule/in',this.$qs.stringify({
          "schedule_id":schedule_id,
          "corps":JSON.stringify(corps)
        })).then((res)=>{

          if(!res.code){
            this.alertFalg.falg=true;
            this.alertFalg.msg='战队添加成功';
            this.getSchedule();
            setTimeout(function(){
              that.alertFalg.falg=false;
            },3000)
          }
        })
      },

      // 移除战队
      removeCorpIn:function(index){
        var that=this;
        // 赛程战队id
        var schedule_corp_id=this.addCorpList.corps[index].schedule_corp_id;

        var json=[schedule_corp_id];
        this.$axios.post('https://dc.3128play.com/api/admin/schedule/out',this.$qs.stringify({
          "id":JSON.stringify(json)
        })).then((res)=>{
          console.log(res);
          this.alertFalg.falg1=true;
          this.alertFalg.msg='战队移除成功';
          this.getSchedule();
          setTimeout(function(){
            that.alertFalg.falg1=false;
          },3000)
        })
      }
    }
  }
</script>
<style scoped>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(-20px);
    opacity: 0;
  }

  .jumbotron{
    padding-top:35px;
    padding-bottom:35px;
    margin-bottom: 10px;
    padding-left: 50px;
    cursor: pointer;
    transition: all .3s;
    line-height: 2.5;
  }
  .logoImg{
    width: 50px;
  }
  .alert{
    position:fixed;
    top:60px;
    width:81%;
  }
  .alert>h4{
    margin:0;
    padding:10px;
  }

</style>



