<template>
	<div class="match">
		<main role="main" class="container-fluid">
      <div class="container-fluid ">
        <div class="row">
          <table class="table table-striped table-hover">
            <tr>
              <th>ID</th>
              <th>战队LOGO</th>
              <th>战队名称</th>
              <th>成员数</th>
              <th>成员上限</th>
              <th>战队口号</th>
              <!--<th>战队描述</th>-->
              <th>积分</th>
              <th>是否需要审核</th>
            </tr>
            <tr v-for="item in array" @click="details(item.id)" style="cursor: pointer" data-toggle="modal" data-target="#myModal">
              <th>{{item.id}}</th>
              <th><img v-bind:src="item.logo"/></th>
              <th>{{item.name}}</th>
              <th>{{item.number}}</th>
              <th>{{item.limit_number}}</th>
              <th>{{item.slogan}}</th>
              <!--<th>{{item.detail}}</th>-->
              <th>{{item.point}}</th>
              <th v-if="item.need_check==0">不需要</th>
              <th v-else>需要</th>
            </tr>
          </table>
        </div>
      </div>
		</main>

    <!--战队详情弹窗-->
    <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" id="myModalLabel">战队详情</h4>
          </div>
          <div class="modal-body">
           <div class="topbs">
             <div class="logs"><img v-bind:src="logo" style="width: 180px"/> </div>
             <div class="yio">
               <p>战队名称：{{names}}</p>
               <p>战队人数：{{numbers}}</p>
               <p>战队上限：{{limit_numbers}}</p>
               <p>战队ID：{{nameid}}</p>
               <p>积分：{{points}}</p>
               <p>是否需要审核：<span v-if="need_checks=1">不需要</span><span v-else>需要</span> </p>
             </div>

           </div>
            <p style="margin-top: 15px">战队口号：{{slogans}}</p>
            <div style="margin-top: 15px">战队描述：{{describe}}</div>
            <table class="table table-hover" style="margin-top: 15px">
              <tr>
                <th>头像</th>
                <th>位置</th>
                <th>昵称</th>
                <th></th>
              </tr>
              <tr v-for="item in array2" >
                <th><img v-bind:src="item.user.avatar"/></th>
                <th>{{item.identify}}</th>
                <th>{{item.user.nick_name}}</th>
                <th></th>
              </tr>
            </table>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
            <!--<button type="button" class="btn btn-primary">Save changes</button>-->
          </div>
        </div>
      </div>
    </div>
    <!--战队详情弹窗-->

	</div>
</template>
<script>

	export default{
		name:'Match',
		data(){
			return{
        array:[],
        array2:[],
        logo:'',
        names:'',
        slogans:'',
        describe:'',
        nameid:'',
        numbers:'',
        limit_numbers:'',
        points:'',
        need_checks:'',
			}
		},
    created(){
		  this.request();
    },
		methods:{
		  //战队详细信息
      details(a){
        var that = this;
        that.$axios.get('https://dc.3128play.com/api/admin/corp/'+a ,that.$qs.stringify({

        })).then((res)=>{
          console.log(res);
          that.logo = res.data.data.logo;
          that.names = res.data.data.name;
          that.slogans = res.data.data.slogan;
          that.describe = res.data.data.detail;
          that.array2 = res.data.data.members;
          that.nameid = res.data.data.id;
          that.numbers = res.data.data.number;
          that.limit_numbers = res.data.data.limit_number;
          that.points = res.data.data.point;
          that.need_checks = res.data.data.need_check;
        }).catch(error =>{
          console.log(error);
        });
      },

      //所有战队信息
      request(){
        var that=this;
        that.$axios.post('https://dc.3128play.com/api/admin/corp',that.$qs.stringify({
         })).then((res)=>{
           console.log(res);
          that.array = res.data.data;
              // console.log(that.array);
         }).catch(error =>{
           console.log(error);
         })
      },
		},

	}


</script>
<style scoped>
  .yio{
    margin-left: 10px;
  }
  .topbs{
    display: flex;
    align-items: center;
  }
  .noon{
    margin-top: 5%;
  }

  th{
    padding: 15px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 1px solid #ddd;
  }
  .table-hover tr:hover {
    background-color: #f5f5f5;
  }
  .table{
    background-color: white;
    border-right: 1px solid #ddd;
    border-left: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }
  .table-striped tr:nth-of-type(odd) {
    background-color: #f9f9f9;
  }
	.position-file>input{
	    position: relative;
	    top: -29px;
	    opacity: 0;
	}
	.pt-3{
		cursor:pointer;
	}
	.mt-3{
		margin-top: 10px;
	}
	.h6, h6 {
	    font-size: 18px;
	    font-weight: 700;
	    color: #6c757d;
	    border-bottom: none;
	}
	.media{
		margin-top:2rem;
	}
	p{
		margin:auto;
		flex:1
	}
	img{
		width: 50px;
	}
	.p-3 {
	    padding: 1rem!important;
	}
	.text-muted {
	    color: #6c757d!important;
	}
	.pb-3, .py-3 {
	    padding-bottom: 1rem!important;
	}
	.d-block {
	    display: block!important;
	}
	.media {
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    -webkit-box-align: start;
	    -ms-flex-align: start;
	    align-items: flex-start;
	}
	.mr-2, .mx-2 {
	    margin-right: .5rem!important;
	    width: 50px;
	    height: 50px;
	    background-color: #007BFF;
	}
	.rounded {
	    border-radius: .25rem!important;
	}
	.bg-white {
	    background-color: #fff!important;
	}

	html,
	body {
	  overflow-x: hidden; /* Prevent scroll on narrow devices */
	}

	body {
	  padding-top: 56px;
	}

	@media (max-width: 767.98px) {
	  .offcanvas-collapse {
	    position: fixed;
	    top: 56px; /* Height of navbar */
	    bottom: 0;
	    width: 100%;
	    padding-right: 1rem;
	    padding-left: 1rem;
	    overflow-y: auto;

	    transition: -webkit-transform .3s ease-in-out;
	    transition: transform .3s ease-in-out;
	    transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
	    -webkit-transform: translateX(100%);
	    transform: translateX(100%);
	  }
	  .offcanvas-collapse.open {
	    -webkit-transform: translateX(-1rem);
	    transform: translateX(-1rem); /* Account for horizontal padding on navbar */
	  }
	}

	.nav-scroller {
	  position: relative;
	  z-index: 2;
	  height: 2.75rem;
	  overflow-y: hidden;
	}

	.nav-scroller .nav {
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  -ms-flex-wrap: nowrap;
	  flex-wrap: nowrap;
	  padding-bottom: 1rem;
	  margin-top: -1px;
	  overflow-x: auto;
	  color: rgba(255, 255, 255, .75);
	  text-align: center;
	  white-space: nowrap;
	  -webkit-overflow-scrolling: touch;
	}

	.nav-underline .nav-link {
	  padding-top: .75rem;
	  padding-bottom: .75rem;
	  font-size: .875rem;

	}

	.nav-underline .nav-link:hover {

	}

	.nav-underline .active {
	  font-weight: 500;

	}

	.text-white-50 { color: rgba(255, 255, 255, .5); }



	.border-bottom { border-bottom: 1px solid #e5e5e5; }

	.box-shadow { box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); }

	.lh-100 { line-height: 1; }
	.lh-125 { line-height: 1.25; }
	.lh-150 { line-height: 1.5; }
</style>



