// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import  Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import axios from 'axios'
import qs from 'qs'
import Vuex from 'vuex'
import element from 'element-ui'

import Activity from './components/Activity/Activity'
import Match from './components/Match/Match'
import MatchEdit from './components/MatchEdit/MatchEdit'
import Login from './components/Login'
import Combination from './components/Combination/Combination'
import Container from './components/Container/Container'
import Member from './components/Member/Member'
import store from './store/store.js'
import Process from './components/Process/Process'
import Schedule from './components/Schedule/Schedule'
import ScheduleItem from './components/Schedule/ScheduleItem'
import 'element-ui/lib/theme-chalk/index.css'
import MerchantCode from './components/MerchantCode/MerchantCode'
import config from './config'

Vue.use(Vuex);
Vue.use(element);
Vue.prototype.$qs = qs;
Vue.prototype.$axios = axios;
Vue.config.productionTip = false;
Vue.prototype.HOST = '/api';
Vue.prototype.api =config.api

Vue.use(VueRouter);
let router=new VueRouter({
	routes:[
		{
			path:'/',
      redirect: "/login"
		},
		{
			path:'/login',
      component: Login,
      meta: {requiresAuth: true}
		},
		{
			path:'/combination',
      component:Combination,
      meta:{requires:true},
			children:[
				{path:'/combination/match',component:Match,meta:{requires:true}},
				{path:'/combination/activity',component:Activity,meta:{requires:true}},
				{path:'/combination/member',component:Member,meta:{requires:true}},
        {path:'/combination/process',component:Process,meta:{requires:true}},
        {path:'/combination/matchedit',component:MatchEdit,meta:{requires:true}},
        {path:'/combination/schedule',component:Schedule,meta:{requires:true}},
        {path:'/combination/scheduleitem',component:ScheduleItem,meta:{requires:true}},
        {path:'/combination/merchantcode',component:MerchantCode,meta:{requires:true}},
			]
		}
	]
});

router.beforeEach((to,from,next) =>{
  // console.log(to)
  if(to.meta.requiresAuth){
    if(window.localStorage.token){
      next('/combination/activity');
    }else{
      next();
    }
  }else if(to.meta.requires){
    if(!window.localStorage.token){
      next('/login');
    }else{
      next();
    }
  }else{
    next();
  }
});

axios.interceptors.request.use(config => {
    if (localStorage.token){  //判断是否存在token，如果存在，每个http header都加上token
      // config.headers.Authorization = `${localStorage.token}`;
      config.headers.Authorization = 'Bearer ' + localStorage.token;
      console.log(config)
      // config.headers['Authorization'] =  'Bearer TrpNjEr11cXqLolsz1yykKfugqQqvo0byg7oIXAli80F7hmtTJ3RBZw6mlSw'
    }
    // console.log(config)
    return config;
  },
  err => {
    return Promise.reject(err);
  });

// http request  拦截器
axios.interceptors.request.use(function (config) {
  config.headers.token = localStorage.getItem("token")//将接口返回的token信息配置到接口请求中
  return config;
},function (error) {
  return Promise.reject(error);
  });
axios.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response){
      switch (error.response.status){
        case 401:
          //返回401清除token信息并跳转到登录页面
          window.localStorage.removeItem('token');
              store.commit(types.LOGOUT);
              router.replace({
                path:'/login',
                query:{redirect: router.currentRoute.fullPath}
              })
      }
    }
    return Promise.reject(error.response.data) //返回接口返回的错误信息
  }
);

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



