<template>
	<div class="login">
		<div class="form-signin text-center">
			<img class="mb-4" src="./../assets/logo.png" alt="" width="110">
			<h1 class="h3 mb-3 font-weight-normal">请输入</h1>
			<input name="username" type="text" id="inputUsername" class="form-control" placeholder="账号名" required autofocus>
			<input name="password" type="password" id="inputPassword" class="form-control" placeholder="密码" required>
			<div class="checkbox mb-3">
				<label>
					<!--<input type="checkbox" value="remember-me">保存密码-->
				</label>
			</div>
      <div >
				<button @click="Login" class="btn btn-lg btn-danger btn-block">登陆</button>
      </div>
		</div>
	</div>
</template>
<script>
  // import config from '../config'
	export default{
		name:'Login',
		data(){
			return{
        router:''
			}
		},
		methods:{
			Login:function(data){
			  var that=this;
				// console.log($('#inputUsername').val(),$('#inputPassword').val());
				let username=$('#inputUsername').val();
				let password=$('#inputPassword').val();
				password = $.md5(password);
				// console.log(password);
				this.$axios.post('https://dc.3128play.com/api/oauth/login',this.$qs.stringify({
          name:username,
					// cellphone:"",
					password:password
				})).then((res)=>{
          console.log(res);
          // console.log(res.config.adapter.name)
          if(res.data.code==-1){
				    alert("账号或密码错误");
          }else if(res.data.code==0){
            window.localStorage.token=res.data.data;
            this.$router.push({path:'/combination/activity'});


          }
				}).catch(error => {
				  console.log(error);
				  // alert("账号或密码错误！")
        });

			}
		}
	}
</script>
<style scoped>
.login{
	height: 100%;
	display: flex;
	align-items: center;
}
.form-signin{
	margin-top: -6rem !important;
}
html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



