<template>
  <div class="activity">
    <main role="main" class="container-fluid">

      <div class="my-3 p-3 bg-white rounded box-shadow" >
        <div class="pase">

          <h6 class=" border-gray pb-2 mb-0">活动</h6>
          <button class="btn btn-info" data-toggle="modal" data-target="#myModal">创建活动</button>
        </div>

        <div v-for="(item,index) in array" class="media text-muted pt-3"  >
          <img v-bind:src="item.cover" alt="" class="mr-2 rounded" style="width: 45px;">
          <div class="media-body pb-3 mb-0 small lh-125 border-bottom border-gray" >
            <strong class="d-block text-gray-dark"> {{item.name}}</strong>
            主办方：{{item.organizer}}
            <br/>
            <p style="margin: 2px 0">起止时间：{{item.begin_at}}～{{item.end_at}}</p>

          </div>
          <button class="btn btn-primary bia" @click="details(item.id)" data-toggle="modal" data-target="#exampleModal">详情</button>
          <button class="btn btn-danger bia" @click="shout(item.id)">操作</button>
          <button class="btn btn-default bia"  @click="edit(item.id)">赛程</button>
          <button class="btn btn-success" v-if="item.is_show==0" @click="show(item.id)">显示</button>
          <button class="btn btn-warning" v-else @click="show(item.id)">隐藏</button>
        </div>

        <small class="d-block text-right mt-3">

        </small>
      </div>

      <!--活动详情弹出框-->
      <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header panel ">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
              <h4 class="" id="exampleModalLabel" style="margin: 0">详情</h4>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-md-6">
                  <div class=" dimen">
                    <span>活动名称：</span><p>{{names}}</p>
                  </div>
                  <div class=" dimen">
                    <span>主办方：</span><p>{{organizers}}</p>
                  </div>
                  <div class=" dimen">
                    <span>活动奖品：</span><p>{{prizes}}</p>
                  </div>
                  <div class=" dimen">
                    <span>举办城市：</span><p>{{citys}}</p>
                  </div>
                  <div class="dimen">
                    <span>赞助商：</span><p>{{sponsors}}</p>
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="dimen">
                    <span>联系人：</span><p>{{contacts}}</p>
                  </div>
                  <div class="dimen">
                    <span>电话：</span><p>{{cellphones}}</p>
                  </div>
                  <div class="dimen">
                    <span>开始时间：</span><p>{{begin_ats}}</p>
                  </div>
                  <div class="dimen">
                    <span>截止时间：</span><p>{{end_ats}}</p>
                  </div>
                    <div class=" dimen">
                      <span>状态：</span><p v-if="(statuss)=='NORMAL'">可报名</p><p v-else>截止报名</p>
                    </div>
                </div>
              </div>

              <div class="row pos">
                <div class="col-md-12 dimen">
                  <span>活动规则：</span><p style="width: 89%">{{detaila}}</p>
                </div>
              </div>
              <div class="row">
                <div class="col-md-6">
                  <span>封面海报：</span>
                  <div class="row">
                    <img v-bind:src="covers" style="width: 60%;margin-left: 10%"/>
                  </div>
                </div>
                <div class="col-sm-6" >
                  <span>详情海报：</span>
                  <div class="row">
                    <img v-bind:src="detail_imagess" style="width: 60%;margin-left: 10%"/>
                  </div>
                </div>
              </div>



              <table class="table table-hover" style="margin-top: 15px">
                <caption>赛程</caption>

                <tr>
                  <th>ID</th>
                  <th>举办城市</th>
                  <th>地址</th>
                  <th>赛程名称</th>
                  <th>开始时间</th>
                  <th>结束时间</th>
                  <th>状态</th>
                </tr>
                <tr v-for="item in scheduless">
                  <th>{{item.id}}</th>
                  <th>{{item.city}}</th>
                  <th>{{item.address}}</th>
                  <th>{{item.name}}</th>
                  <th>{{item.begin_at}}</th>
                  <th>{{item.end_at}}</th>

                  <th v-if="item.status=='NORMAL'">进行中</th>
                  <th v-else>已结束</th>
                </tr>

              </table>

              <table class="table table-hover" style="margin-top: 15px">
                <caption>报名战队</caption>

                <tr>
                  <th>ID</th>
                  <th>战队logo</th>
                  <th>战队名称</th>
                  <th>成员</th>
                  <th>成员上限</th>
                  <th>战队口号</th>
                  <th>战队描述</th>
                  <th>积分</th>
                  <th>审核</th>
                </tr>
                <tr v-for="(item,index) in corpss">
                  <th>{{item.id}}</th>
                  <th><img v-bind:src="item.logo" style="width: 32px;height: 32px"/></th>
                  <th>{{item.name}}</th>
                  <th>{{item.number}}</th>
                  <th>{{item.limit_number}}</th>
                  <th>{{item.slogan}}</th>
                  <th>{{item.detail}}</th>
                  <th>{{item.point}}</th>
                  <th v-if="item.need_check==0">不需要</th>
                  <th v-else>需要</th>
                </tr>

              </table>

            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
              <!--<button type="button" class="btn btn-primary" data-dismiss="modal" @click="modify">保存</button>-->
              <!--<button type="button" class="btn btn-danger" data-dismiss="modal" @click="delet">删除</button>-->
            </div>
          </div>
        </div>
      </div>

      <!--创建活动弹出框-->
      <div class="modal fade" tabindex="-1" id="myModal" role="dialog" aria-labelledby="gridSystemModalLabel">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header panel-success">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
              <h4 class="modal-title" id="gridSystemModalLabel">发布活动</h4>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-md-6 ">
                  <div class="dimen2"><span>活动名称<span class="reds">*</span>：</span><input  type="text" id="name" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>活动奖品<span class="reds">*</span>：</span><input  type="text" id="prize" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>主办方：</span><input  type="text" id="organizer" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>举办城市：</span><input  type="text" id="city" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>赞助商：</span><input  type="text" id="sponsor" class="form-control"  placeholder=""></div>

                </div>
                <div class="col-md-6">
                  <div class="dimen2"><span>联系人：</span><input  type="text" id="contact" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>电话：</span><input  type="text" id="cellphone" class="form-control"  placeholder=""></div>
                  <div class="dimen2"> <span>开始时间<span class="reds">*</span>：</span><input  type="date" id="begin_at" class="form-control"  placeholder=""></div>
                  <div class="dimen2"><span>截止时间<span class="reds">*</span>：</span><input id="end_at" type="date"  class="form-control"  placeholder=""></div>

                </div>
              </div>

                <div class="col-md-12">
                  <div class="dimen2">
                    <p style="width: 71px">活动规则<span class="reds">*</span>：</p>
                    <textarea id="detail" class="form-control" rows="3"  placeholder=""></textarea>
                  </div>
                </div>

              <div class="row">
                <div class="col-sm-6 zix">
                  <div class="position-file">
                    <button class="btn" style="margin-bottom: 10px" type="file">封面图片<span class="reds">*</span></button>
                    <el-upload
                    class="avatar-uploader"
                    action="https://dc.3128play.com/api/upload-image"
                    name="img"
                    :headers="uploadHeader"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </el-upload>
                </div>
              </div>
                <div class="col-sm-6 zix">
                  <button class="btn dimen2" type="file">详情图片</button>
                  <div class="position-file">
                    <el-upload
                      class="avatar-uploader"
                      action="https://dc.3128play.com/api/upload-image"
                      name="img"
                      :headers="uploadHeader"
                      :show-file-list="false"
                      :on-success="handleAvatarSuccess1"
                      :before-upload="beforeAvatarUpload1">
                      <img v-if="imageUrl1" :src="imageUrl1" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>


                    <!--<el-upload-->
                    <!--action="/api/api/upload-image"-->
                    <!--name="img"-->
                    <!--list-type="picture-card"-->
                    <!--:on-preview="handlePictureCardPreview"-->
                    <!--:on-success="handleRemove"-->
                    <!--:headers="uploadHeader"-->
                    <!--:limit="1"-->
                    <!--&gt;-->
                    <!--<i class="el-icon-plus"></i>-->
                    <!--</el-upload>-->
                    <!--<el-dialog :visible.sync="dialogVisible">-->
                    <!--<img width="100%" :src="dialogImageUrl" alt="">-->
                    <!--</el-dialog>-->
                  </div>
                </div>
            </div>
            <div class="row">
           <div class="zix doms">
             <p>带星号项是必填项</p>
           </div>
          </div>
        </div>
        <div class="modal-footer">

          <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-primary" data-dismiss="modal" @click="establish">提交</button>
        </div>
      </div>
    </div>
  </div>

</main>
</div>
</template>
<script>
  import a from './../../export.js'

  export default{
    name:'Activity',
    data(){
      return{
        a:'',
        b:'',
        imgUrl:'',
        index:'',
        array:[],
        names:'',
        prizes:'',
        organizers:'',
        citys:'',
        contacts:'',
        detaila:'',
        detail_imagess:'',
        covers:'',
        begin_ats:'',
        cellphones:'',
        end_ats:'',
        sponsors:'',
        statuss:'',
        corpss:[],
        scheduless:[],
        imageUrl: '',
        imageUrl1:'',
        dialogImageUrl: '',
        dialogVisible: false,
        uploadHeader: {
          Accept: 'application/json',
          Authorization: 'Bearer '+localStorage.token
        },
        imgs:'',
        img2:'',
        UrlImgArray:[],
        cd_id:'',
        // 活动id
        schedule_id:''
      }

    },
    created(){
      this.seal()
    },
    methods:{

      //切换活动显示隐藏
      show:function (index) {
        var ids = index;
        var that=this;
        that.$axios.post('https://dc.3128play.com/api/admin/activity/show',that.$qs.stringify({
          activity_id:ids,
        })).then((res)=>{
          console.log(res);
          that.seal();
        }).catch((error)=>{
          console.log(error);
        })
      },

      //所有活动列表
      seal:function () {
        var that=this;
        that.$axios.post('https://dc.3128play.com/api/admin/activity',that.$qs.stringify({

        })).then((res)=>{
          console.log(res);
          that.array = res.data.data;
          that.cd_id = res.data.data.id
        }).catch((error)=>{
          console.log(error);
        });

      },
      //上传详情图片
      handleAvatarSuccess1(res, file) {
        this.imageUrl1 = URL.createObjectURL(file.raw);
        this.img2=file.response.data;
        console.log(this.img2)
      },
      beforeAvatarUpload1(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },

      //上传封面图片
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        this.imgs=file.response.data;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      //创建活动
      establish:function () {
        var that = this;
        if ($('#name').val()==""&&$('#begin_at').val()==""){
          alert("带*号项必填！")
        }else if($("#end_at").val()==""&&$("#detail").val()==""){
          alert("带*号项必填！")
        }else if ($("#prize").val()==""){
          alert("带*号项必填！")
        }else {
          // console.log($(".el-upload-list__item-thumbnail")[0].src)
          that.$axios.post("https://dc.3128play.com/api/admin/activity/create",that.$qs.stringify({
            name :$("#name").val() ,
            cover : that.imgs,
            begin_at:$("#begin_at").val() ,
            end_at:$("#end_at").val() ,
            detail:$("#detail").val() ,
            detail_images:that.img2,
            prize:$("#prize").val(),
            organizer:$("#organizer").val() ,
            sponsor:$("#sponsor").val() ,
            city:$("#city").val() ,
            contact:$("#contact").val() ,
            cellphone:$("#cellphone").val() ,
          })).then((res)=>{
            console.log(res);
            that.seal()
          }).catch(error=>{
            console.log(error);
          });
        }

      },

      //活动的详细信息
      details(a){
        var that = this;
        // 活动id赋值
        this.schedule_id=a;
        that.$axios.get('/api/api/admin/activity/'+a).then((res)=>{
          // a.asd=res.data.data
          console.log(res);
          that.names = res.data.data.name;
          that.prizes = res.data.data.prize;
          that.organizers = res.data.data.organizer;
          that.citys = res.data.data.city;
          that.contacts = res.data.data.contact;
          that.detaila = res.data.data.detail;
          that.detail_imagess = res.data.data.detail_images;
          that.begin_ats = res.data.data.begin_at;
          that.cellphones = res.data.data.cellphone;
          that.end_ats = res.data.data.end_at;
          that.sponsors = res.data.data.sponsor;
          that.statuss = res.data.data.status;
          that.corpss = res.data.data.corps;
          that.covers = res.data.data.cover;
          that.scheduless = res.data.data.schedules;
        }).catch((error) =>{
          console.log(error);
        })
      },
   //操作按钮传参到修改活动页面
    shout:function (index) {
      console.log(index)
      this.index=index;
        // this.$emit('index',this.array[index]);
        this.$router.push({path:'/combination/process?id='+index})
      },
      //赛程按钮传参到赛程页面
      edit:function(index){
        console.log(index);
        this.index=index;
        this.$router.push({path:'/combination/schedule?id='+index})
      },
      // modify:function () {
      //   var that=this;
      //   let name = $("#recipient-name").val();
      //   let text = $("#message-text").val();
      //   console.log(name);
      //   console.log(text);
      //   this.lai.splice(that.index,1,{a:name,b:text});
      //   // this.lai.$set(0,b);
      // },
      // delet:function (index) {
      //   var that=this;
      //   this.lai.splice(that.index,1)
      //
      // },

   },

 }


</script>
